import React, { useState } from 'react';
import { projects } from '../data/mockData';
import { ExternalLink, Github, ChevronLeft, ChevronRight } from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle } from './ui/card';
import { Badge } from './ui/badge';
import { Button } from './ui/button';

const Projects = () => {
  const [selectedProject, setSelectedProject] = useState(projects[0]);
  const [activeTab, setActiveTab] = useState('overview');

  const nextProject = () => {
    const currentIndex = projects.findIndex(p => p.id === selectedProject.id);
    const nextIndex = (currentIndex + 1) % projects.length;
    setSelectedProject(projects[nextIndex]);
    setActiveTab('overview');
  };

  const prevProject = () => {
    const currentIndex = projects.findIndex(p => p.id === selectedProject.id);
    const prevIndex = currentIndex === 0 ? projects.length - 1 : currentIndex - 1;
    setSelectedProject(projects[prevIndex]);
    setActiveTab('overview');
  };

  const getCategoryColor = (category) => {
    const colors = {
      'E-commerce': 'bg-red-500/20 text-red-200 border-red-400/30',
      'Microservices': 'bg-rose-500/20 text-rose-200 border-rose-400/30',
      'Entrepreneurship': 'bg-orange-500/20 text-orange-200 border-orange-400/30'
    };
    return colors[category] || 'bg-gray-500/20 text-gray-200 border-gray-400/30';
  };

  return (
    <section id="projects" className="py-20 px-4 sm:px-6 lg:px-8 bg-slate-800/50">
      <div className="max-w-7xl mx-auto">
        <div className="text-center mb-16">
          <h2 className="text-4xl md:text-5xl font-bold text-white mb-6">
            Featured Projects
          </h2>
          <div className="w-24 h-1 bg-gradient-to-r from-red-500 to-orange-500 mx-auto mb-6"></div>
          <p className="text-xl text-gray-300 max-w-3xl mx-auto">
            Showcasing innovative solutions that demonstrate expertise in technical architecture, entrepreneurship, and product development.
          </p>
        </div>

        {/* Project Selector */}
        <div className="grid md:grid-cols-3 gap-6 mb-12">
          {projects.map((project) => (
            <Card 
              key={project.id}
              className={`cursor-pointer transition-all duration-300 transform hover:scale-105 ${
                selectedProject.id === project.id 
                  ? 'bg-slate-700 border-red-400' 
                  : 'bg-slate-800/50 border-slate-700 hover:bg-slate-800/70'
              }`}
              onClick={() => {
                setSelectedProject(project);
                setActiveTab('overview');
              }}
            >
              <CardHeader className="p-4">
                <div className="relative overflow-hidden rounded-lg mb-4">
                  <img 
                    src={project.image} 
                    alt={project.title}
                    className="w-full h-32 object-cover"
                  />
                  <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent"></div>
                </div>
                <CardTitle className="text-lg text-white mb-2">{project.title}</CardTitle>
                <Badge className={`w-fit ${getCategoryColor(project.category)}`}>
                  {project.category}
                </Badge>
              </CardHeader>
            </Card>
          ))}
        </div>

        {/* Project Details */}
        <Card className="bg-slate-800/70 border-slate-700">
          <CardHeader>
            <div className="flex items-center justify-between mb-4">
              <Button
                variant="outline"
                size="sm"
                onClick={prevProject}
                className="border-slate-600 text-gray-300 hover:bg-slate-700"
              >
                <ChevronLeft size={16} className="mr-1" />
                Previous
              </Button>
              <Badge className={`${getCategoryColor(selectedProject.category)}`}>
                {selectedProject.category}
              </Badge>
              <Button
                variant="outline"
                size="sm"
                onClick={nextProject}
                className="border-slate-600 text-gray-300 hover:bg-slate-700"
              >
                Next
                <ChevronRight size={16} className="ml-1" />
              </Button>
            </div>
          
            <CardTitle className="text-3xl text-white mb-4">{selectedProject.title}</CardTitle>
          
          {/* Tabs */}
          <div className="flex gap-4 mb-6">
            {['overview', 'challenges', 'solutions'].map((tab) => (
              <button
                key={tab}
                onClick={() => setActiveTab(tab)}
                className={`px-4 py-2 rounded-lg font-medium transition-all duration-300 capitalize ${
                  activeTab === tab 
                    ? 'bg-red-500 text-white' 
                    : 'text-gray-400 hover:text-white hover:bg-slate-700'
                }`}
              >
                {tab}
              </button>
            ))}
          </div>
        </CardHeader>

        <CardContent className="space-y-6">
          {/* Project Image */}
          <div className="relative overflow-hidden rounded-xl">
            <img 
              src={selectedProject.image} 
              alt={selectedProject.title}
              className="w-full h-64 md:h-80 object-cover"
            />
            <div className="absolute inset-0 bg-gradient-to-t from-black/40 to-transparent"></div>
          </div>

          {/* Tab Content */}
          {activeTab === 'overview' && (
            <div className="space-y-6">
              <p className="text-lg text-gray-300 leading-relaxed">
                {selectedProject.description}
              </p>

              {/* Technologies */}
              <div>
                <h4 className="text-xl font-semibold text-white mb-3">Technologies Used</h4>
                <div className="flex flex-wrap gap-2">
                  {selectedProject.technologies.map((tech, index) => (
                    <Badge 
                      key={index} 
                      variant="secondary" 
                      className="bg-slate-700 text-gray-300"
                    >
                      {tech}
                    </Badge>
                  ))}
                </div>
              </div>

              {/* Metrics */}
              <div>
                <h4 className="text-xl font-semibold text-white mb-3">Key Metrics</h4>
                <div className="grid md:grid-cols-3 gap-4">
                  {selectedProject.metrics.map((metric, index) => (
                    <div key={index} className="bg-slate-700/50 rounded-lg p-4 text-center">
                      <div className="text-2xl font-bold text-red-400">{metric}</div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          )}

          {activeTab === 'challenges' && (
            <div>
              <h4 className="text-xl font-semibold text-white mb-4">Key Challenges</h4>
              <ul className="space-y-3">
                {selectedProject.challenges.map((challenge, index) => (
                  <li key={index} className="flex items-start text-gray-300">
                    <div className="w-2 h-2 bg-rose-500 rounded-full mt-2 mr-3 flex-shrink-0"></div>
                    <span className="text-lg">{challenge}</span>
                  </li>
                ))}
              </ul>
            </div>
          )}

          {activeTab === 'solutions' && (
            <div>
              <h4 className="text-xl font-semibold text-white mb-4">Solutions Implemented</h4>
              <ul className="space-y-3">
                {selectedProject.solutions.map((solution, index) => (
                  <li key={index} className="flex items-start text-gray-300">
                    <div className="w-2 h-2 bg-orange-500 rounded-full mt-2 mr-3 flex-shrink-0"></div>
                    <span className="text-lg">{solution}</span>
                  </li>
                ))}
              </ul>
            </div>
          )}

          {/* Action Buttons */}
          <div className="flex gap-4 pt-6">
            <Button className="bg-red-500 hover:bg-red-600">
              <ExternalLink size={16} className="mr-2" />
              View Details
            </Button>
            <Button variant="outline" className="border-slate-600 text-gray-300 hover:bg-slate-700">
              <Github size={16} className="mr-2" />
              Source Code
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  </section>
  );
};

export default Projects;