import React from 'react';
import { experience, certifications } from '../data/mockData';
import { MapPin, Calendar, Award, ExternalLink } from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle } from './ui/card';
import { Badge } from './ui/badge';

const Experience = () => {
  return (
    <section id="experience" className="py-20 px-4 sm:px-6 lg:px-8">
      <div className="max-w-6xl mx-auto">
        <div className="text-center mb-16">
          <h2 className="text-4xl md:text-5xl font-bold text-white mb-6">
            Professional Journey
          </h2>
          <div className="w-24 h-1 bg-gradient-to-r from-red-500 to-orange-500 mx-auto mb-6"></div>
          <p className="text-xl text-gray-300 max-w-3xl mx-auto">
            A track record of leading technical innovations and delivering impactful solutions across diverse industries.
          </p>
        </div>

        {/* Experience Timeline */}
        <div className="space-y-8 mb-20">
          {experience.map((job, index) => (
            <Card key={job.id} className="bg-slate-800/50 border-slate-700 hover:bg-slate-800/70 transition-all duration-300">
              <CardHeader>
                <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4">
                  <div>
                    <CardTitle className="text-2xl text-white mb-2">{job.position}</CardTitle>
                    <div className="text-xl text-red-400 font-semibold">{job.company}</div>
                  </div>
                  <div className="flex flex-col md:items-end gap-2">
                    <div className="flex items-center text-gray-300">
                      <Calendar size={16} className="mr-2 text-orange-400" />
                      {job.duration}
                    </div>
                    <div className="flex items-center text-gray-300">
                      <MapPin size={16} className="mr-2 text-red-400" />
                      {job.location}
                    </div>
                  </div>
                </div>
              </CardHeader>
              <CardContent className="space-y-6">
                {/* Key Achievements */}
                <div>
                  <h4 className="text-lg font-semibold text-white mb-4">Key Achievements</h4>
                  <ul className="space-y-3">
                    {job.achievements.map((achievement, achIndex) => (
                      <li key={achIndex} className="flex items-start text-gray-300">
                        <div className="w-2 h-2 bg-red-400 rounded-full mt-2 mr-3 flex-shrink-0"></div>
                        <span>{achievement}</span>
                      </li>
                    ))}
                  </ul>
                </div>

                {/* Technologies */}
                <div>
                  <h4 className="text-lg font-semibold text-white mb-3">Core Technologies</h4>
                  <div className="flex flex-wrap gap-2">
                    {job.technologies.map((tech, techIndex) => (
                      <Badge 
                        key={techIndex} 
                        variant="secondary" 
                        className="bg-red-500/20 text-red-200 border-red-400/30"
                      >
                        {tech}
                      </Badge>
                    ))}
                  </div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>

        {/* Certifications Section */}
        <div>
          <div className="text-center mb-12">
            <h3 className="text-3xl font-bold text-white mb-4">
              Professional Certifications
            </h3>
            <div className="w-16 h-1 bg-gradient-to-r from-rose-500 to-orange-500 mx-auto"></div>
          </div>

          <div className="grid md:grid-cols-2 gap-6">
            {certifications.map((cert, index) => (
              <Card key={index} className="bg-slate-800/50 border-slate-700 hover:bg-slate-800/70 transition-all duration-300">
                <CardHeader className="flex flex-row items-center space-y-0 pb-2">
                  <Award className="w-6 h-6 text-amber-400 mr-3" />
                  <div className="flex-1">
                    <CardTitle className="text-lg text-white">{cert.name}</CardTitle>
                    <p className="text-sm text-gray-400">{cert.issuer}</p>
                  </div>
                  <div className="text-right">
                    <div className="text-sm font-semibold text-red-400">{cert.year}</div>
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="flex items-center justify-between">
                    <span className="text-xs text-gray-500">Credential ID: {cert.credentialId}</span>
                    <ExternalLink size={14} className="text-gray-400" />
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default Experience;